import React from 'react';
import '../../App.css';
import './css.css'
import {App, f7ready, Views} from "framework7-react";
import {MainView} from "./views/mainview/MainView";

window.__MUI_USE_NEXT_TYPOGRAPHY_VARIANTS__ = true;
window.React = React;
window.onload = function () {
    tellParentHeight();
}
window.addEventListener("resize",()=>{
    // tellParentHeight();
});

function tellParentHeight(){
    var div = document.getElementById("pagecontent");
    if(!div)return;
    var h = div.offsetHeight;
    console.log('div is', h, div);
    if(!window.parent)return;

    // var w = div.offsetWidth;    // 返回元素的总宽度
    // var h = div.offsetHeight;    // 返回元素的总高度
    // var h = document.body.clientHeight;
    window.parent.postMessage(h, "*");
}

class Main extends React.Component {
    _addEvents() {
    }

    render = () => {
        f7ready((f7) => {
            // console.log("f7ready", f7.view.main.router.history.length, f7.view.main.router.history.concat());
        })

        return (
            <App id={"app"} >
                <Views className={"safe-areas"}>
                    <MainView />
                    {/*<View view-init main {...mainViewParams}>*/}
                    {/*    <MainPage/>*/}
                    {/*</View>*/}
                </Views>

            </App>
        )
    }
}

export default Main;
